
#app .tab {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  height: 40px;
  line-height: 40px;
  position: relative;
}
#app .tab:after {
    content: '';
    display: block;
    width: 100%;
    position: absolute;
    bottom: 0;
    left: 0;
    border: 1px solid rgba(7, 17, 27, 0.1);
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}
#app .tab .tab-item {
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
    text-align: center;
}
#app .tab .tab-item > a {
      display: block;
      font-size: 14px;
      color: #4d555d;
}
#app .tab .tab-item > a.active {
        color: #f01414;
}



