
@charset "UTF-8";
.shopcart {
  width: 100%;
  height: 48px;
  position: fixed;
  left: 0;
  bottom: 0;
  z-index: 50;
  font-size: 0;
}
.shopcart .shopcart-wrapper {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    background: #141d27;
}
.shopcart .shopcart-wrapper .content-left {
      -webkit-box-flex: 1;
          -ms-flex: 1;
              flex: 1;
      /* 价钱 */
}
.shopcart .shopcart-wrapper .content-left .logo-wrapper {
        display: inline-block;
        width: 56px;
        height: 56px;
        position: relative;
        top: -10px;
        margin: 0 12px;
        padding: 6px;
        border-radius: 50%;
        -webkit-box-sizing: border-box;
                box-sizing: border-box;
        vertical-align: top;
        background: #141d27;
}
.shopcart .shopcart-wrapper .content-left .logo-wrapper .logo {
          width: 100%;
          height: 100%;
          border-radius: 50%;
          background: #2d343d;
          text-align: center;
}
.shopcart .shopcart-wrapper .content-left .logo-wrapper .logo .icon {
            font-size: 24px;
            line-height: 44px;
            color: #80858a;
}
.shopcart .shopcart-wrapper .content-left .logo-wrapper .logo.highlight {
            background: #00a0dc;
}
.shopcart .shopcart-wrapper .content-left .logo-wrapper .logo.highlight .icon {
              color: #fff;
}
.shopcart .shopcart-wrapper .content-left .num {
        position: absolute;
        top: 0;
        right: 0;
        width: 24px;
        height: 16px;
        line-height: 16px;
        text-align: center;
        border-radius: 16px;
        background: red;
        color: #fff;
        font-size: 9px;
        font-weight: 700px;
        -webkit-box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.4);
                box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.4);
}
.shopcart .shopcart-wrapper .content-left .price {
        display: inline-block;
        height: 24px;
        line-height: 24px;
        vertical-align: top;
        margin: 12px 0;
        padding-right: 12px;
        color: rgba(255, 255, 255, 0.4);
        border-right: 1px solid rgba(255, 255, 255, 0.1);
        -webkit-box-sizing: border-box;
                box-sizing: border-box;
        font-size: 16px;
        font-weight: 700;
}
.shopcart .shopcart-wrapper .content-left .price.hightlight {
          color: #fff;
}
.shopcart .shopcart-wrapper .content-left .desc {
        height: 24px;
        line-height: 24px;
        display: inline-block;
        vertical-align: top;
        font-size: 10px;
        color: rgba(255, 255, 255, 0.4);
        margin: 12px 12px 0;
}
.shopcart .shopcart-wrapper .content-right {
      -webkit-box-flex: 0;
          -ms-flex: 0 0 105px;
              flex: 0 0 105px;
      width: 105px;
}
.shopcart .shopcart-wrapper .content-right .pay {
        width: 100%;
        height: 48px;
        font-size: 12px;
        font-weight: 700;
        line-height: 48px;
        text-align: center;
}
.shopcart .shopcart-wrapper .content-right .pay.not-enough {
          background: #2b333b;
          color: rgba(255, 255, 255, 0.1);
}
.shopcart .shopcart-wrapper .content-right .pay.enough {
          background: #00b43c;
          color: #fff;
}
.shopcart .ball-container .ball {
    position: fixed;
    left: 32px;
    bottom: 22px;
    z-index: 200;
    -webkit-transition: all 0.4s;
    transition: all 0.4s;
}
.shopcart .ball-container .ball.drop-enter, .shopcart .ball-container .ball.drop-enter-active {
      -webkit-transition: all 0.4s cubic-bezier(0.49, -0.29, 0.75, 0.41);
      transition: all 0.4s cubic-bezier(0.49, -0.29, 0.75, 0.41);
}
.shopcart .ball-container .ball.drop-enter .inner, .shopcart .ball-container .ball.drop-enter-active .inner {
        width: 16px;
        height: 16px;
        border-radius: 50%;
        background: #00a0dc;
        -webkit-transition: all 0.4s linear;
        transition: all 0.4s linear;
}
.shopcart .shopcart-list {
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
    width: 100%;
    -webkit-transition: all 0.5s;
    transition: all 0.5s;
    -webkit-transform: translate3d(0, -100%, 0);
            transform: translate3d(0, -100%, 0);
}
.shopcart .shopcart-list.fold-enter-active, .shopcart .shopcart-list.fold-leave-active {
      -webkit-transition: all 0.5s;
      transition: all 0.5s;
}
.shopcart .shopcart-list.fold-enter, .shopcart .shopcart-list.fold-leave-active {
      -webkit-transform: translate3d(0, 0, 0);
              transform: translate3d(0, 0, 0);
}
.shopcart .shopcart-list .list-header {
      height: 40px;
      line-height: 40px;
      padding: 0 18px;
      background: #f3f5f7;
      border-bottom: 2px solid rgba(7, 17, 27, 0.1);
}
.shopcart .shopcart-list .list-header .title {
        float: left;
        font-size: 14px;
        color: #07111b;
}
.shopcart .shopcart-list .list-header .empty {
        float: right;
        font-size: 12px;
        color: #00a0dc;
}
.shopcart .shopcart-list .list-content {
      padding: 0 18px;
      max-height: 217px;
      background: #fff;
      overflow: hidden;
}
.shopcart .shopcart-list .list-content .food {
        position: relative;
        padding: 12px 0;
        -webkit-box-sizing: border-box;
                box-sizing: border-box;
        position: relative;
}
.shopcart .shopcart-list .list-content .food:after {
          content: '';
          display: block;
          width: 100%;
          position: absolute;
          bottom: 0;
          left: 0;
          border: 1px solid rgba(7, 17, 27, 0.1);
          -webkit-box-sizing: border-box;
                  box-sizing: border-box;
}
.shopcart .shopcart-list .list-content .food .name {
          line-height: 24px;
          font-size: 14px;
          color: #07111b;
}
.shopcart .shopcart-list .list-content .food .price {
          position: absolute;
          bottom: 12px;
          right: 90px;
          line-height: 24px;
          font-size: 14px;
          color: #f01414;
          font-weight: 700;
}
.shopcart .shopcart-list .list-content .food .cartcontrol-wrapper {
          position: absolute;
          bottom: 6px;
          right: 0;
}
.list-mask {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 40;
  -webkit-backdrop-filter: blur(10px);
          backdrop-filter: blur(10px);
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
  opacity: 1;
  background: rgba(7, 17, 27, 0.6);
}
.list-mask.listMask-enter-active, .list-mask.listMask-leave-active {
    -webkit-transition: all 0.5s;
    transition: all 0.5s;
}
.list-mask.listMask-enter, .list-mask.listMask-leave-active {
    opacity: 0;
    background: rgba(7, 17, 27, 0);
}



